import React from 'react'
import { useParams } from 'react-router-dom'
import ProjectData from '../types/Project.type'
import MediaData from '../types/Media.type'
import LightGallery from 'lightgallery/react';
import ReactMarkdown from 'react-markdown'
import remarkGfm from 'remark-gfm'
import { LightGallerySettings } from 'lightgallery/lg-settings';
import 'lightgallery/scss/lightgallery.scss';
import 'lightgallery/scss/lg-zoom.scss';
import lgThumbnail from 'lightgallery/plugins/thumbnail';
import lgZoom from 'lightgallery/plugins/zoom'
import lgVideo from 'lightgallery/plugins/video'
import MediaCard from '../components/Media.component'
import useImagePreloader from '../hooks/UseImagePreloadrer'
import Animation from '../components/Animation.component'

import { getProject } from '../services/api'

const ProjectDetail: React.FC = () => {
  const { title } = useParams() 
  const project: any = getProject( title )
  const mediaUrls = project.media.map( ( med: MediaData ) => {
    return med.url
  }  )

  const { imagesPreloaded } = useImagePreloader( mediaUrls )

  return (
    <>
      <div 
        className='client-detail'
        style={ { background: project.theme.background } }
        >
        <section 
          className='client-details-hero'
          style={ { background: `url(${project.hero})` } }
          >
          <div
            className='hero-detail'
            style={ { backgroundImage: `url(${project.herodetail})` } }
            >
          </div>
          <div className='section-inner'>
            <div className='section-middle'>
              <Animation>
                { project.logo 
                  ? <div 
                    className='logo'
                    style={ { backgroundImage: `url(${ project.logo })` } }
                    ></div> 
                  : '' }
                <h1
                  className='balance-text'>
                  { project.title }
                </h1>
                <h2
                  className='balance-text'>
                  { project.introduction }
                </h2>
              </Animation>
              { project.hero ? <div className='hero'></div> : '' }
            </div>
          </div>
        </section>
        <section className='gallery-section'> 
            <LightGallery
              mode="lg-fade"
              licenseKey='EBF3B338-B5D7433E-87880993-1BF41F57'
              plugins={[lgVideo]}
              >
              { project.media
                ? (
                  <>
                    { project.media && project.media.map( ( med: MediaData, index: number ) => ( 
                      <>
                        { med.mediatype === 'video'
                          ? (
                            <a 
                              className="gallery-item"
                              data-src={ med.source }
                              data-poster={ med.poster }
                              >
                              <MediaCard {...med} key={index} />
                            </a>
                          ) : (
                            <a 
                              className="gallery-item"
                              href={ med.url }
                              key={ index }
                              >
                              <MediaCard {...med} key={index}/>
                            </a>
                          ) }
                      </>
                    ) ) } 
                  </>
                ) : (
                  ''
                )}
            </LightGallery>
        </section>
        <section>
          <div className='section-inner'>
            <div className='section-middle'>
              <Animation>
                { project.description 
                  ? <ReactMarkdown remarkPlugins={[remarkGfm]}>{ project.description }</ReactMarkdown>
                  : ''
                }
              </Animation>
            </div>
          </div>
        </section>
      </div>
    </>
  )
}

export default ProjectDetail
